.nav-container {
    padding: 1rem;
    background-color: var(--secondary-color);

    display: flex;
    align-items: center;
}

.nav-hamburger {
    flex: 1;
    display: none;
}

.nav-logo {
    flex: 2;
    font-size: 170%;
    font-weight: 800;
}

.nav-list {
    flex: 4;
}

.nav-options {
    flex: 2;
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
}

/* --------------------   RESPONIVE   ---------------- */
@media all and (max-width: 600px) {
    .nav-hamburger {
        display: block;
    }

    .nav-list {
        display: none;
    }
}