.news-container {
    width: 100%;
    padding: 1rem;

    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
}

@media all and (max-width: 600px) {
    .news-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media all and (max-width: 450px) {
    .news-container {
        grid-template-columns: repeat(1, 1fr);
    }
}