a {
    text-decoration: none;
}

.news-box {
    width: 100%;
    height: 455px;
    padding: 1rem;
    border-radius: 10px;
    background-color: var(--secondary-color);
    color: var(--primary-text);
    overflow: hidden;
}

.news-box>img {
    width: 100%;
}

.news-box>h3 {
    color: var(--primary-text);
    font-weight: 400;
}

.news-box>p {
    color: var(--secondary-text);
}

/* @media all and (max-width: 600px) {
    .news-box {
        height: min-content;
    }
} */

@media all and (max-width: 450px) {
    .news-box {
        height: min-content;
    }
}